import { create } from 'zustand';
import { persist } from 'zustand/middleware';

interface Campaign {
  id: string;
  name: string;
  status: 'active' | 'paused';
  stats: {
    clicks: number;
    impressions: number;
    ctr: number;
    cpc: number;
    cpm: number;
  };
}

interface User {
  id: string;
  username: string;
  avatar: string;
  tariff: 'freelancer' | 'company';
  facebookConnected: boolean;
}

interface AppState {
  // 
  theme: 'light' | 'dark';
  setTheme: (theme: 'light' | 'dark') => void;

  // 
  campaigns: Campaign[];
  addCampaign: (campaign: Campaign) => void;
  updateCampaign: (id: string, campaign: Partial<Campaign>) => void;
  deleteCampaign: (id: string) => void;

  // 
  user: User | null;
  setUser: (user: User | null) => void;
  updateUser: (user: Partial<User>) => void;

  // 
  isAutoPilotEnabled: boolean;
  toggleAutoPilot: () => void;

  // 
  notifications: {
    message: string;
    type: 'info' | 'success' | 'error' | 'warning';
  }[];
  addNotification: (message: string, type: 'info' | 'success' | 'error' | 'warning') => void;
  removeNotification: (index: number) => void;
}

export const useStore = create<AppState>()(
  persist(
    (set) => ({
      // 
      theme: 'light',
      setTheme: (theme) => set({ theme }),

      // 
      campaigns: [],
      addCampaign: (campaign) =>
        set((state) => ({ campaigns: [...state.campaigns, campaign] })),
      updateCampaign: (id, campaign) =>
        set((state) => ({
          campaigns: state.campaigns.map((c) =>
            c.id === id ? { ...c, ...campaign } : c
          ),
        })),
      deleteCampaign: (id) =>
        set((state) => ({
          campaigns: state.campaigns.filter((c) => c.id !== id),
        })),

      // 
      user: null,
      setUser: (user) => set({ user }),
      updateUser: (user) =>
        set((state) => ({
          user: state.user ? { ...state.user, ...user } : null,
        })),

      // 
      isAutoPilotEnabled: false,
      toggleAutoPilot: () =>
        set((state) => ({ isAutoPilotEnabled: !state.isAutoPilotEnabled })),

      // 
      notifications: [],
      addNotification: (message, type) =>
        set((state) => ({
          notifications: [...state.notifications, { message, type }],
        })),
      removeNotification: (index) =>
        set((state) => ({
          notifications: state.notifications.filter((_, i) => i !== index),
        })),
    }),
    {
      name: 'app-storage',
      partialize: (state) => ({
        theme: state.theme,
        user: state.user,
        isAutoPilotEnabled: state.isAutoPilotEnabled,
      }),
    }
  )
); 